
                    @import '~backline-mixins/src/backline-mixins';
                    @import 'settings';
                  
.HotspotFrame {
  height: 100%;
  overflow: hidden;
  pointer-events: none;
}

.HotspotFrameList {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  pointer-events: none;
}

.HotspotFrameItem {
  position: absolute;
  width: 1px;
  height: 1px;
  pointer-events: auto;
}

.debug {
  &.HotspotFrame {
    pointer-events: auto;
  }
}
