.ImageFrame {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: $color-grey1;
  background-image: linear-gradient(124deg, $color-grey1 0%, $color-white 100%);

  > img {
    max-width: 100%;
    max-height: 100%;
  }
}
